<template>
  <div class="home">
    <!-- serach -->
    <van-search
      v-model="value"
      placeholder="请输入搜索关键词"
      input-align="center"
      shape="round"
      background="#4fc08d"
      @search="searchData"
    />
    <ul v-for="item in datalist" :key="item.name">
      <li @click="tiaozhuan(item)">{{ item.name }}</li>
    </ul>
    <!-- swiper -->
    <van-swipe :autoplay="3000">
      <van-swipe-item
        class="van-swipe-item"
        v-for="item in images"
        :key="item.imageUrl"
      >
        <img :src="item.imageUrl" />
      </van-swipe-item>
    </van-swipe>
    <!-- grid -->
    <van-grid :column-num="4">
      <van-grid-item
        v-for="value in categories"
        :key="value.id"
        :icon="value.coverImage | dalImg"
        :text="value.name"
        :to="{ name: 'list', query: { t: value.id } }"
      />
      <!-- card -->
    </van-grid>
    <van-list
      v-model="loading"
      :finished="finished"
      finished-text="没有更多了"
      @load="loadData"
      class="content"
    >
      <van-card
        v-for="(item, index) in products"
        :key="item.uuid"
        num="1"
        :tag="index + 1 + ''"
        :price="item.price.toFixed(2)"
        :desc="item.desc"
        :title="item.name"
        :thumb="item.coverImage"
        :origin-price="(item.price * 1.2).toFixed(2)"
        @click-thumb="toDetail(item)"
      />
    </van-list>
  </div>
</template>

<script>
// @ is an alias to /src
import axios from "axios";
import { loadCategoriesAPI } from "../services/categories";
import { loadProductsAPI, loadSearchDataAPI } from "../services/products";
export default {
  name: "HomeView",
  data() {
    return {
      value: "",
      datalist: [],
      images: [],
      categories: [],
      products: [],
      page: 1,
      pages: 1,
      loading: false,
      finished: false,
    };
  },
  components: {},
  mounted() {
    axios.get("https://net-music.penkuoer.com/banner").then((res) => {
      this.images = res.data.banners;
    });
  },
  created() {
    this.loadData();
  },
  methods: {
    tiaozhuan(item) {
      this.$router.push({
        name: "detail",
        params: {
          id: item.id,
        },
      });
    },
    async searchData() {
      const res = await loadSearchDataAPI();
      console.log(res);
      this.datalist = res.data;
    },
    async loadData() {
      // 分类
      const resCategories = await loadCategoriesAPI();
      this.categories = resCategories.data;
      // 商品列表
      const resProducts = await loadProductsAPI(this.page, this.$route.query.t);
      this.page++;
      this.pages = resProducts.pages;
      this.products.push(...resProducts.data);
      this.loading = false;
      if (this.page > this.pages) {
        this.finished = true;
      }
    },
    // 商品详情页
    toDetail(item) {
      this.$router.push({
        name: "detail",
        params: {
          id: item.id,
        },
      });
    },
  },
};
</script>
