<template>
  <van-tabbar v-show="isShowTabBar" v-model="active" :fixed="false" route>
    <van-tabbar-item name="home" icon="home-o" :to="{ name: 'home' }"
      >首页</van-tabbar-item
    >
    <van-tabbar-item name="search" icon="fire-o" :to="{ name: 'list' }"
      >热卖</van-tabbar-item
    >
    <van-tabbar-item name="friends" icon="cart-o" :to="{ name: 'cart' }"
      >购物车</van-tabbar-item
    >
    <van-tabbar-item name="setting" icon="user-o" :to="{ name: 'user' }"
      >用户</van-tabbar-item
    >
  </van-tabbar>
</template>

<script>
export default {
  data() {
    return {
      active: "home",
      isShowTabBar: true,
    };
  },
  // 监听
  watch: {
    $route(v) {
      // 判断当前跳转的页面是否需要隐藏底部导航
      if (v.meta.hideTabBar) {
        this.isShowTabBar = false;
      } else {
        this.isShowTabBar = true;
      }
    },
  },
};
</script>

