import Vue from "vue";
import Vant from "vant";
import "vant/lib/index.css";

import App from "./App.vue";
import router from "./router";
import {serverUrl} from "./utils/request"
Vue.use(Vant);
Vue.config.productionTip = false;

// 全局过滤器
Vue.filter('dalImg', (url) => {
  if (url) {
    if (url.startsWith("http")) {
      return url;
    }
    return serverUrl + url;
  } else {
    return "https://gimg2.baidu.com/image_search/src=http%3A%2F%2Fimg.jj20.com%2Fup%2Fallimg%2F1111%2F05161Q64001%2F1P516164001-3-1200.jpg&refer=http%3A%2F%2Fimg.jj20.com&app=2002&size=f9999,10000&q=a80&n=0&g=0n&fmt=auto?sec=1652320995&t=2ed5b060fa40721da102bc9b892a17f3";
  }
});
new Vue({
  router,
  render: (h) => h(App),
}).$mount("#app");
