import { get } from "../utils/request";
import { post } from "../utils/request";
import { del } from "../utils/request";

/**
 * 获取商品列表
 * @param {*} page
 * @param {*} category
 * @returns
 */
export const loadProductAPI = (page, per) =>
  get("/api/v1/products", { page, per });
export const loadProductsAPI = (page = 1, category) =>
  get("/api/v1/products", { page, category });
/**
 * 获取商品详情 根据id
 * @param {*} id
 * @returns
 */
export const loadproductDetailAPI = (id) => get("/api/v1/products/" + id);
export const loadSearchDataAPI = () => get("/api/v1/products");

/**
 * 加入购物车
 * @param {*} product
 * @param {*} amount
 * @param {*} price
 * @returns
 */
export const loadAddCart = (product, amount, price) =>
  post("/api/v1/shop_carts", { product, amount, price });

//获取购物车列表
export const cartListAPI = () => get("/api/v1/shop_carts");

//删除购物车商品
export const delCartListAPI = (id) => del("/api/v1/shop_carts/" + id);
